<!---->
<template>
  <v-container dense>
    <v-card elevation="0" class="ma-1">
      <!--  -->
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
            <v-col cols="3">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0">
                <small></small>
              </v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
            <v-col cols="2">
              <v-subheader class="ma-0 mt-n5 pa-0"></v-subheader>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4"></div>
            </v-col>
            <v-col cols="3">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                @mousedown="showDatePicker('1')"
                v-model="fruitValueSproutTreatment.date"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueSproutTreatment.weight"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueSproutTreatment.count"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn
                class="primary ma-0 mt-n12 pl-1 pr-1"
                @click="saveUseFruitValueSproutTreatment"
              ></v-btn>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4"></div>
            </v-col>
            <v-col cols="3">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                @mousedown="showDatePicker('2')"
                v-model="fruitValueELStage.date"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueELStage.weight"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueELStage.count"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn class="primary ma-0 mt-n12 pl-1 pr-1" @click="saveUseFruitValueELStage"></v-btn>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="10">
          <v-row>
            <v-col cols="3">
              <div class="ma-0 mt-n5 pa-0 pl-4"></div>
            </v-col>
            <v-col cols="3">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                @mousedown="showDatePicker('3')"
                v-model="fruitValueBagging.date"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueBagging.weight"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-text-field
                class="ma-0 mt-n8 pl-1 pr-1 text_field_size"
                dense
                hide-details="auto"
                outlined
                v-model.number="fruitValueBagging.count"
              ></v-text-field>
            </v-col>
            <v-col cols="2">
              <v-btn class="primary ma-0 mt-n12 pl-1 pr-1" @click="saveUseFruitValueBagging"></v-btn>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-card>
    <v-dialog v-model="isDateDialog" width="auto">
      <v-date-picker
        v-model="dateInfo.date"
        :min="dateInfo.minDate"
        :max="dateInfo.maxDate"
        @change="hideDatePicker"
        locale="jp-ja"
      ></v-date-picker>
    </v-dialog>
  </v-container>
</template>

<script>
import { useFruitValue, useFruitValueUpdate } from "@/api/TopStateGrowth/index";
import moment from "moment";

export default {
  props: {
    selectedMenu: Object,
    selectedField: Array,
    selectedYears: Array,
    selectedDevices: Array,
  },

  data() {
    return {
      isDateDialog: false,
      devicesId: null, // ID
      year: null, //

      //
      fruitValueSproutTreatment: {
        date: moment().format("YYYY-MM-DD"), //
        weight: null, //
        count: null, //
      },
      //E-L 2731
      fruitValueELStage: {
        date: moment().format("YYYY-MM-DD"), //
        weight: null, //
        count: null, //
      },
      //
      fruitValueBagging: {
        date: moment().format("YYYY-MM-DD"), //
        weight: null, //
        count: null, //
      },

      dateInfo: {
        date: null,
        minDate: null,
        maxDate: null,
        eventId: null,
      },
    };
  },
  components: {},
  mounted() {
    this.$emit("mounted");
  },
  methods: {
    //* ============================================
    // 
    //* ============================================
    setEmptyData(date) {
      this.fruitValueSproutTreatment = {
        date: date, //
        weight: null, //
        count: null, //
      };
      //E-L 2731
      this.fruitValueELStage = {
        date: date, //
        weight: null, //
        count: null, //
      };
      //
      this.fruitValueBagging = {
        date: date, //
        weight: null, //
        count: null, //
      };
    },
    initialize: function (menu) {
      this.devicesId = menu.selectedDevice.id;
      this.year = menu.selectedYear.id;
      this.getFruitValue(this.devicesId, this.year);
    },
    //* ============================================
    // 
    //* ============================================
    getFruitValue(device, year) {
      useFruitValue(device, year)
        .then((response) => {
          //
          const { status, message, data } = response["data"];
          if (status === 0) {
            this.dateInfo.minDate = data.startDate;
            this.dateInfo.maxDate = data.endDate;
            let today = moment().format("YYYY-MM-DD");
            if (this.dateInfo.maxDate > today) {
              this.setEmptyData(today);
            } else {
              this.setEmptyData(data.endDate);
            }
            for (const item of data.values) {
              if (item.eventId == 1) {
                this.fruitValueSproutTreatment.date = item.targetDate;
                this.fruitValueSproutTreatment.weight = item.average;
                this.fruitValueSproutTreatment.count = item.count;
              } else if (item.eventId == 2) {
                this.fruitValueELStage.date = item.targetDate;
                this.fruitValueELStage.weight = item.average;
                this.fruitValueELStage.count = item.count;
              } else if (item.eventId == 3) {
                this.fruitValueBagging.date = item.targetDate;
                this.fruitValueBagging.weight = item.average;
                this.fruitValueBagging.count = item.count;
              }
            }
          } else {
            throw new Error(message);
          }
        })
        .catch((error) => {
          alert("");
          console.log(error);
        });
    },

    //* ============================================
    // 
    //* ============================================
    saveUseFruitValueSproutTreatment: function () {
      const data = {
        deviceId: parseInt(this.devicesId),
        year: this.year,
        eventId: 1,
        ...this.fruitValueSproutTreatment,
      };
      this.updateFruitValue(data);
    },
    //* ============================================
    // 
    //* ============================================
    saveUseFruitValueELStage: function () {
      const data = {
        deviceId: parseInt(this.devicesId),
        year: this.year,
        eventId: 2,
        ...this.fruitValueELStage,
      };
      this.updateFruitValue(data);
    },
    //* ============================================
    // 
    //* ============================================
    saveUseFruitValueBagging: function () {
      const data = {
        deviceId: parseInt(this.devicesId),
        year: this.year,
        eventId: 3,
        ...this.fruitValueBagging,
      };
      this.updateFruitValue(data);
    },
    //* ============================================
    // API
    //* ============================================
    updateFruitValue(data) {
      //
      useFruitValueUpdate(data)
        .then((response) => {
          const { status, message } = response["data"];
          if (status === 0) {
            alert("");
          } else {
            throw new Error(message);
          }
        })
        .catch((error) => {
          alert("");
          console.log(error);
        });
    },
    //* ============================================
    // 
    //* ============================================
    showDatePicker(eventId) {
      //* 
      this.dateInfo.eventId = eventId;
      if (eventId == 1) {
        this.dateInfo.date = this.fruitValueSproutTreatment.date;
      } else if (eventId == 2) {
        this.dateInfo.date = this.fruitValueELStage.date;
      } else if (eventId == 3) {
        this.dateInfo.date = this.fruitValueBagging.date;
      }
      this.isDateDialog = true;
    },
    //* ============================================
    // 
    //* ============================================
    hideDatePicker() {
      this.isDateDialog = false;
      if (this.dateInfo.eventId == 1) {
        this.fruitValueSproutTreatment.date = this.dateInfo.date;
      } else if (this.dateInfo.eventId == 2) {
        this.fruitValueELStage.date = this.dateInfo.date;
      } else if (this.dateInfo.eventId == 3) {
        this.fruitValueBagging.date = this.dateInfo.date;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/style/common.css";

.divider_top {
  margin-bottom: 10px;
  color: "black";
}

.divider_center {
  margin-top: 10px;
  margin-bottom: 10px;
  color: "black";
}

.divider_bottom {
  margin-top: 10px;
  color: "black";
}

.text_field_size {
  min-width: 60px;
}
</style>
