<template>
  <v-app>
    <v-dialog v-model="isDialog" width="60%" persistent>
      <v-card>
        <v-card-title></v-card-title>

        <v-card-text>
          <v-data-table :headers="headers" :items="devices" dense elevation-6></v-data-table>
        </v-card-text>

        <v-card-actions>
          <v-btn
            color="primary"
            class="ma-2 white--text"
            elevation="2"
            @click="confirmAction()"
            :disabled="isRunning == true"
          ></v-btn>

          <v-btn
            color="primary"
            class="ma-2 white--text"
            elevation="2"
            @click="callLoaderSelected()"
            :disabled="isRunning == true"
          >()</v-btn>

          <v-btn elevation="2" @click="isDialog = false" :disabled="isRunning == true"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <confirmDailog :shared="sharedConfirm" ref="confirm" />
  </v-app>
</template>
  
<script>
import {
  useGetSchedule,
  useLoadData,
  useGetAllDataLoadStatus,
} from "@/api/ManagementScreenTop/MSDevice";
import moment from "moment";
import { DialogController } from "@/lib/mountController.js";
import confirmDailog from "@/components-v1/parts/dialog/confirmDialog";

const HEADERS = [
  { text: "", value: "name", sortable: true, width: "40%" },
  { text: "ID", value: "id", sortable: true, width: "10%" },
  { text: "", value: "status", sortable: true, width: "50%" },
];

export default {
  props: { shared /** DialogController */: { required: true } },

  data() {
    return {
      sharedConfirm: new DialogController(),

      headers: HEADERS,
      devices: [],
      isDialog: false,
      isRunning: false,
      now: null,
      count: 0,
    };
  },
  components: {
    confirmDailog,
  },
  mounted() {
    this.shared.mount(this);
  },
  methods: {
    // ======================================================
    // 
    // ======================================================
    initialize: function (devices) {
      this.isDialog = true; // 
      this.setDisplay(devices); // 
    },
    // ======================================================
    // 
    // ======================================================
    setDisplay: function (devices) {
      this.isRunning = false; // 

      this.devices.length = 0;
      for (const device of devices) {
        var item = {
          id: device.id,
          name: device.name,
          status: "",
        };
        this.devices.unshift(item);
      }
    },
    // ======================================================
    // 
    // ======================================================
    confirmAction() {
      this.sharedConfirm.setUp(
        this.$refs.confirm,
        function (confirm) {
          confirm.initialize(
            "10",
            "",
            ""
          );
        },
        function (action) {
          if (action) {
            this.callLoaderAPI();
          }
        }.bind(this)
      );
    },
    // ======================================================
    // API
    // ======================================================
    callLoaderAPI: function () {
      this.isRunning = true;
      // * 
      useGetSchedule(null)
        .then((response) => {
          const { status, message, data } = response["data"];
          if (status != 0) {
            throw new Error(message);
          }
          this.now = moment().format("YYYY-MM-DD");
          this.doLoaderList(data);
        })
        .catch((error) => {
          //
          alert("");
          console.log(error);
          this.isRunning = false;
        });
    },
    // ======================================================
    // API
    // ======================================================
    callLoaderSelected: function () {
      this.isRunning = true;
      // * 
      useGetSchedule(null)
        .then((response) => {
          const { status, message, data } = response["data"];
          if (status != 0) {
            throw new Error(message);
          }
          let list = [];
          for (const device of data) {
            if ((device.dataStatus & 8) == 0) {
              list.push(device);
            }
          }
          this.now = moment().format("YYYY-MM-DD");
          this.doLoaderList(list);
        })
        .catch((error) => {
          //
          alert("");
          console.log(error);
          this.isRunning = false;
        });
    },
    // ======================================================
    // 
    // ======================================================
    doLoaderList: function (list) {
      this.isRunning = true;
      this.count = list.length;
      for (const device of list) {
        this.callLoader(device.id);
      }
    },
    // ======================================================
    // API
    // ======================================================
    callLoader: function (deviceId) {
      // * 
      this.changeStatus(deviceId, "");
      // * API
      useLoadData(deviceId)
        .then((response) => {
          console.log(response);
          this.count--;
          if (this.count == 0) {
            this.checkDataStatus();
          }
        })
        .catch((error) => {
          console.log(error);
          this.count--;
          if (this.count == 0) {
            this.checkDataStatus();
          }
        });
    },

    // ======================================================
    // 
    // ======================================================
    checkDataStatus: function () {
      this.intervalid1 = setInterval(
        function () {
          // API
          this.callApiGetAllDataLoadStatus(this.now);
          // isRunningfalse
          if (!this.isRunning) {
            clearInterval(this.intervalid1);
          }
        }.bind(this),
        3000
      );
    },
    // ======================================================
    // API
    // ======================================================
    callApiGetAllDataLoadStatus(startTime) {
      useGetAllDataLoadStatus(startTime)
        .then((response) => {
          const { status, message, data } = response["data"];
          let nowobj = moment(this.now);
          if (status != 0) {
            throw new Error(message);
          }
          let isRunning = false;
          for (const item of data) {
            if ((status & 1) > 0) {
              isRunning = true;
              this.changeStatus(item.id, "");
            } else {
              if (null == item.date) {
                this.changeStatus(
                  item.id,
                  ""
                );
              } else {
                let statusTime = moment(item.date);
                if (statusTime.isBefore(nowobj)) continue;
                this.changeStatus(item.id, this.checkStatus(item.status));
              }
            }
          }
          this.isRunning = isRunning;
        })
        .catch((error) => {
          //
          alert("");
          console.log(error);
          this.isRunning = false;
        });
    },
    // ======================================================
    // 
    // ======================================================
    checkStatus(status) {
      if (status == -1) {
        return "";
      } else if ((status & 8) > 0) {
        return "";
      } else {
        return "";
      }
    },
    // ======================================================
    // 
    //  ID
    //  
    // ======================================================
    changeStatus(deviceId, message) {
      for (const item of this.devices) {
        if (item.id == deviceId) {
          item.status = message;
          break;
        }
      }
    },
  },
};
</script>
<style lang="scss">
</style>
  